<template>
  <div class="" style="padding:0;margin:0;background:#ECEFF4;">
<br>   
<br>   
    <div class="row justify-content-center animate__animated animate__backInUp" style="height:100vh;">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header">
            <div class="text-center mx-auto">
            <img src="@/static/icon.png" class="kinoLightBox img-fluid " style="height:30px;">
            </div>
          </div>
          <div class="card-body shadow-lg rounded-lg">
            <p class="text-center font-bold font-times">REGISTER</p>
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <form action="#" @submit.prevent="submit">
              <div class="form-group row">
                <label for="name" class="col-md-4 col-form-label text-md-right">Name</label>

                <div class="col-md-6">
                  <input
                    id="name"
                    type="name"
                    class="form-control"
                    name="name"
                    value
                    required
                    autofocus
                    v-model="vdata.name"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="email" class="col-md-4 col-form-label text-md-right">Username</label>

                <div class="col-md-6">
                  <input
                    id="text"
                    type="text"
                    class="form-control"
                    name="email"
                    value
                    required
                    autofocus
                    v-model="vdata.email"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="vdata.password"
                  />
                </div>
              </div>
  <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Reenter Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="vdata.password2"
                  />
                <p class="font-bold text-red-600 p-1" v-show="vdata.password!=vdata.password2">Password tidak sama!</p>

                </div>
              </div>
              <div class="form-group row mb-0">
                <div class="col-md-8 offset-md-4">
                  <button type="submit" class="btn btn-primary"><span class="typcn typcn-key"></span> Register</button>
                </div>
              </div>
              
            </form>
            <hr class="style13">
            <div class="text-center">
              Kembali ke :
                  <button type="submit" @click="$router.push('/auth/login')" class="btn btn-style9 "><span class="typcn typcn-key"></span> Login</button>
            </div>
          </div>
        </div>
      </div>
    </div>
     
  </div>
</template>
<script>
import firebase from "firebase";
let db = firebase.firestore();
// let rdb = firebase.database()
export default {
  data() {
    return {
      vdata: {
        name: "",
        email: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    submit() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.vdata.email+'@gmail.com', this.vdata.password)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.vdata.name
            })
            .then(() => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
            changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }
              };
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true })
                .then(res => {
                  console.log("Login Berhasil");
                  this.$router.push("/");
                });
            });
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>